import back from '@/components/back'
import comSelect from '@/components/select'

export default {
    components:{
        back,
        comSelect
    },
    data(){
        return{
            a: false,
            b: false,
            c: false,
            d: false,
            e: false,
            f: false,
            num: 1,
            num2: 1,
            usechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.UsageMethod : ''
            },
            sizechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.DayDosage : ''
            },
            timechoose: {
                UsageValue: 0,
                UsageName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.UsageTime : ''
            },
            unitchoose: {
                CategoryItemValue: 0,
                CategoryItemName: this.$store.state.cMed.RecommendUsage ? this.$store.state.cMed.RecommendUsage.DosageUnit : ''
            },
            numchoose: {
                CategoryItemValue: 0,
                CategoryItemName: this.$store.state.cMed.PackageUnit
            },
            longchoose: '',
            size: [],
            useTime: [],
            useType: [],
            numType: [],
            longType: [],
            info: this.$store.state.cMed,
            Remark: '',
            Unit: [],
            flag: null,
            addNew: null,
            newName: '',
            flag2: false,
			showConfirm:false,
			showTitle:'',
        }
    },
    watch: {
        sizechoose(){
            if(
                this.$store.state.cMed.RecommendUsage
                && this.$store.state.cMed.RecommendUsage.DosageUnit == this.unitchoose.CategoryItemName
                && this.$store.state.cMed.PackageUnit == this.numchoose.CategoryItemName
            ){
                if(this.longchoose && this.sizechoose){
                    if(!this.$store.state.cMed.PackageCount){
                        return
                    }
                    this.num2 = Math.ceil(this.sizechoose.ExtendValue * this.num * this.longchoose.ExtendValue / this.$store.state.cMed.PackageCount)
                    if(!this.num2){
                       // this.num2 = 1
                    }
                }
            }
        },
        num(){
            if(
                this.$store.state.cMed.RecommendUsage
                && this.$store.state.cMed.RecommendUsage.DosageUnit == this.unitchoose.CategoryItemName
                && this.$store.state.cMed.PackageUnit == this.numchoose.CategoryItemName
            ){
                if(this.longchoose && this.sizechoose){
                    if(!this.$store.state.cMed.PackageCount){
                        return
                    }
                    this.num2 = Math.ceil(this.sizechoose.ExtendValue * this.num * this.longchoose.ExtendValue / this.$store.state.cMed.PackageCount)
                    if(!this.num2){
                       // this.num2 = 1
                    }
                }
            }
        },
        longchoose(){
            if(
                this.$store.state.cMed.RecommendUsage
                && this.$store.state.cMed.RecommendUsage.DosageUnit == this.unitchoose.CategoryItemName
                && this.$store.state.cMed.PackageUnit == this.numchoose.CategoryItemName
            ){
                if(this.longchoose && this.sizechoose){
                    if(!this.$store.state.cMed.PackageCount){
                        return
                    }
                    this.num2 = Math.ceil(this.sizechoose.ExtendValue * this.num * this.longchoose.ExtendValue / this.$store.state.cMed.PackageCount)
                    // console.log(this.sizechoose.ExtendValue, this.num,this.longchoose.ExtendValue, this.$store.state.cMed.PackageCount)
                    if(!this.num2){
                       // this.num2 = 1
                    }
                }
            }
        },
        unitchoose(n){
            if(this.$store.state.cMed.RecommendUsage
                && this.$store.state.cMed.RecommendUsage.DosageUnitId != this.unitchoose.CategoryItemValue){
               // this.num2 = 1
            }
            if(n.CategoryItemValue == 20){
                this.flag2 = true
            }else{
                this.flag2 = false
            }
        },
        // numchoose(){
        //     if(this.$store.state.cMed.PackageUnit != this.numchoose.CategoryItemName){
        //         this.num2 = 1
        //     }
        // }
    },
    created(){
        this.$store.dispatch('pushsaveFlag', 0)
        this.pull()
        const { cMed } = this.$store.state
        this.num = cMed.RecommendUsage ? cMed.RecommendUsage.Dosage : 1
        this.num2 = cMed.Quantity || 1
        this.Remark = cMed.Remark
    },
    methods: {
        show(f){
            this.flag = f
            this[f] = true
        },
		operate(type, num){
            if(this.flag2 && num == 'num'){
                return
            }
            if(type === 'del' && this[num] > 1){
                this[num] -= 1
            }
            else if(type === 'add'){
                this[num] = +(this[num]) + 1
            }
        },
        pick(data){
            this[this.flag] = false
            if(this.flag == 'a'){
                if(data.CategoryItemValue == 20){
                    this.num = 0
                    this.flag2 = true
                }
                else{
                    this.flag2 = false
                }
                this.unitchoose = data || ''
            }
            if(this.flag == 'b'){
                this.sizechoose = data || ''
            }
            else if(this.flag == 'c'){
                this.timechoose = data || ''
            }
            else if(this.flag == 'd'){ 
                this.usechoose = data || ''
            }
            else if(this.flag == 'e'){
                this.numchoose = data || ''
            }
            else if(this.flag == 'f'){
                this.longchoose = data || ''
            }
			this.addNew = null
        },
        pull(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds:[ 12, 38 ]
            }).then(o => {
                o.forEach(v => {
                    const { cMed } = this.$store.state
                    if(v.CategoryId === 12){
                        this.Unit = v.Items
                        this.Unit.forEach(a => {
                            if(cMed.RecommendUsage && a.CategoryItemValue == cMed.RecommendUsage.DosageUnitId){
                                this.unitchoose = a
                            }
                        })
                    }
                    else if(v.CategoryId === 38){
                        this.numType = v.Items
                        this.numType.forEach(a => {
                            if(a.CategoryItemName == cMed.PackageUnit){
                                this.numchoose = a
                            }
                        })
                    }
                })
            })
            this.$post('Prescription/Medication/GetMedicationUsage').then(o => {
                o.Data.forEach(v => {
                    const { cMed } = this.$store.state
                    if(v.UsageType === 3){
                        this.size.push(v)
                        this.size.forEach(a => {
                            if(cMed.RecommendUsage && a.UsageValue == cMed.RecommendUsage.DayDosageId){
                                this.sizechoose = a
                            }
                        })
                    }
                    else if(v.UsageType === 1){
                        this.useTime.push(v)
                        this.useTime.forEach(a => {
                            if(cMed.RecommendUsage && a.UsageValue == cMed.RecommendUsage.UsageTimeId){
                                this.timechoose = a
                            }
                        })
                    }
                    else if(v.UsageType === 2){
                        this.useType.push(v)
                        this.useType.forEach(a => {
                            if(cMed.RecommendUsage && a.UsageValue == cMed.RecommendUsage.UsageMethodId){
                                this.usechoose = a
                            }
                        })
                    }
                    else if(v.UsageType === 4){
                        this.longType.push(v)
                    }
                })
            })
        },
        save(){
            if(!this.usechoose.UsageName){
                return this.$toast('请选择给药途径')
            }
            if(!this.sizechoose.UsageName){
                return this.$toast('请选择每日次数')
            }
            if(!this.num || this.num <= 0){
                if(this.unitchoose.CategoryItemName != '适量'){
                    return this.$toast('请选择每次用量')
                }
            }
            if(!this.unitchoose.CategoryItemName){
                return this.$toast('请选择用量单位')
            }
            if(!this.num2){
                return this.$toast('请选择药品数量')
            }
            if(this.num2%1!==0 || this.num2<1){
                return this.$toast('药品数量必须为整数')
            }
            if(this.$store.state.cMed.Stock < this.num2){
				this.showConfirm = true
				this.showTitle = '药品最大库存为'+this.$store.state.cMed.Stock
				return
			}
            if(!this.numchoose.CategoryItemName){
                return this.$toast('请选择数量单位')
            }
			
			this.$store.dispatch('pushpullFlag', 1)
            this.info.UsageTimeId = this.timechoose ? this.timechoose.UsageValue : 0
            this.info.UsageTime = this.timechoose ? this.timechoose.UsageName : null
            this.info.UsageMethodId = this.usechoose.UsageValue
            this.info.UsageMethod = this.usechoose.UsageName
            this.info.DayDosageId = this.sizechoose.UsageValue
            this.info.DayDosage = this.sizechoose.UsageName
            this.info.PackageUnitId = this.numchoose.CategoryItemValue
            this.info.PackageUnit = this.numchoose.CategoryItemName
            this.info.Dosage = this.num
            this.info.Quantity = this.num2
            this.info.Remark = this.Remark
            this.info.DosageUnit = this.unitchoose.CategoryItemName
            this.info.DosageUnitId = this.unitchoose.CategoryItemValue
            this.info.DosageDays = this.longchoose ? this.longchoose.UsageName : ''
            this.info.DosageDaysId = this.longchoose ? this.longchoose.UsageValue : 0
            const idx = this.$route.query.idx
            this.$store.dispatch('pushsaveFlag', 1)
            if(idx){
                const list = this.$store.state.upList
				this.$store.dispatch('pushmmedNum', 1)
                list.splice(idx, 1, this.info)
                this.$store.dispatch('pushupList', list)
                this.$store.dispatch('pushcMed', null)
            }
            else{
				 var list = this.$store.state.upList
				if(this.$store.state.upListNum.num && list.length >= this.$store.state.upListNum.num){
					list.forEach((v,k) => {
						if(v.MedicationId == this.$store.state.upListNum.id){
							list.splice(k,1)
						}
					})
				}
				this.$store.dispatch('pushmmedNum', 1)
                this.$store.state.upList.push(this.info)
                this.$store.dispatch('pushcMed', this.info)
            }
            this.$store.dispatch('pushpullFlag', 1)
            this.$store.dispatch('pushIsFresh', false)
            this.$router.back()
        },
        back(){
            this.$store.dispatch('pushcMed', null)
            this.$router.back()
        },
        cancel(){
            this.addNew = null
            if(this.addNew == 'size'){
                this.sizechoose = null
            }
            else if(this.addNew == 'Unit'){
                this.unitchoose = null
            }
            else if(this.addNew == 'useTime'){
                this.timechoose = null
            }
            else if(this.addNew == 'useType'){
                this.usechoose = null
            }
            else if(this.addNew == 'numType'){
                this.numchoose = null
            }
            else if(this.addNew == 'longType'){
                this.longchoose = null
            }
        },
        confirm(){
            if(!this.newName){
                return this.$toast('请输入自定义内容')
            }
            if(this.addNew == 'size'){
                this.size.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.sizechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'Unit'){
                this.Unit.push({
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                })
                this.unitchoose = {
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                }
            }
            else if(this.addNew == 'useTime'){
                this.useTime.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.timechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'useType'){
                this.useType.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.usechoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            else if(this.addNew == 'numType'){
                this.numType.push({
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                })
                this.numchoose = {
                    DosageUnitId: 0,
                    CategoryItemValue: 0,
                    CategoryItemName: this.newName
                }
            }
            else if(this.addNew == 'longType'){
                this.longType.push({
                    UsageValue: 0,
                    UsageName: this.newName
                })
                this.longchoose = {
                    UsageValue: 0,
                    UsageName: this.newName
                }
            }
            this.newName = ''
            this.addNew = null
            this.a = false
            this.b = false
            this.c = false
            this.d = false
            this.e = false
            this.f = false
        }
	},
}



